import React from 'react'
import { graphql } from 'gatsby'
import Img from 'gatsby-image'

const AtAGlance = ({ title, items }) => {
    const lis = items.map((item, i) =>
        <li
            className="my-4"
            key={title + i}>
            {item}
        </li>
    )
    return (
        <div>
            <h4 className="my-4">{title}</h4>
            <ul>
                {lis}
            </ul>
        </div>
    )
}

const ProblemStatement = ({ problem, solution }) =>
    <>
        <div className="my-4 text-H3">
            <h3 className="inline">Problem: </h3>
            <p className="inline">{problem}</p>
        </div>
        <div className="my-4 text-H3">
            <h3 className="inline">Solution: </h3>
            <p className="inline">{solution}</p>
        </div>
    </>

const Overview = ({ overview, title }) => {
    const images = overview.image.map((image) =>
        <div><Img fixed={image.childImageSharp.fixed} /></div>
    )
    return (
        <>
            <div className="flex flex-no-wrap w-full h-full overflow-x-scroll">
                {images}
            </div>

            <h1 className="my-4">{title}</h1>

            <AtAGlance
                title="Areas of Focus"
                items={overview.areasOfFocus} />

            <AtAGlance
                title="Important Details"
                items={overview.importantDetails} />

            <ProblemStatement
                problem={overview.problem}
                solution={overview.solution} />
        </>
    )
}

export const overviewFragment = graphql`
  fragment Overview on ContentYaml {
    overview {
        image {
            childImageSharp {
                fluid(maxWidth: 1280) {
                    ...GatsbyImageSharpFluid
                }
                fixed(width: 200) {
                    ...GatsbyImageSharpFixed
                }
            }
        }
        areasOfFocus
        importantDetails
        problem
        solution
      }
  }
`

export default Overview
